.playerCard {
    width: 100%;
    margin: auto;
    max-width: 800px;
    box-sizing: border-box;
    background: #2a2a2a;
    color: #ffffff;
    border-radius: 100px 10px 10px 100px;
    display: flex;
    gap: 20px;
    transition: all 0.5s;
}
.playerCard:hover {
    background: #3b3b3b;
}
.pos {
    display: grid;
    place-items: center;
    min-width: 40px !important;
    min-height: 40px !important;
    border-radius: 100%;
    font-size: 20px;
    font-family: Impact;
}
.teamData {
    width: 100% -100px;
    height: 40px;
    display: flex;
    gap: 10px;
    flex-wrap: wrap;
}
.teamName {
    margin: 0;
    width: fit-content;
    font-family: Impact;
    font-size: 25px;
}
.teamPlayers {
    line-height: 30px;
    color: #d3d3d3;
    font-size: 20px;
}
  
.hide {
    display: none;
}